.app-container__dial {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 640px;
  height: 640px;
  border-radius: 50%;
  background-color: var(--primary-color);
  box-shadow: var(--shadow);
}

.dial__digit-container {
  position: relative;
  color: var(--complementary-colors);
  width: 100%;
  height: 100%;
  font-size: 2rem;
}

.digit-container__digit {
  margin: 0;
  position: absolute;
  display: flex;
  justify-content: center;
  width: 100%;
  height: 100%;
}

.digit-container__digit:nth-child(1) {
  transform: rotate(30deg);
}

.digit-container__digit:nth-child(2) {
  transform: rotate(60deg);
}

.digit-container__digit:nth-child(3) {
  transform: rotate(90deg);
}

.digit-container__digit:nth-child(4) {
  transform: rotate(120deg);
}

.digit-container__digit:nth-child(5) {
  transform: rotate(150deg);
}

.digit-container__digit:nth-child(6) {
  transform: rotate(180deg);
}

.digit-container__digit:nth-child(7) {
  transform: rotate(210deg);
}

.digit-container__digit:nth-child(8) {
  transform: rotate(240deg);
}

.digit-container__digit:nth-child(9) {
  transform: rotate(270deg);
}

.digit-container__digit:nth-child(10) {
  transform: rotate(300deg);
}

.digit-container__digit:nth-child(11) {
  transform: rotate(330deg);
}

.digit__number {
  align-self: start;
}

.digit__number-1 {
  transform: rotate(-30deg);
}

.digit__number-2 {
  transform: rotate(-60deg);
}


.digit__number-3 {
  transform: rotate(-90deg);
}


.digit__number-4 {
  transform: rotate(-120deg);
}


.digit__number-5 {
  transform: rotate(-150deg);
}


.digit__number-6 {
  transform: rotate(-180deg);
}


.digit__number-7 {
  transform: rotate(-210deg);
}


.digit__number-8 {
  transform: rotate(-240deg);
}


.digit__number-9 {
  transform: rotate(-270deg);
}

.digit__number-10 {
  transform: rotate(-300deg);
}


.digit__number-11 {
  transform: rotate(-330deg);
}

.dial__central-axis {
  position: absolute;
  display: flex;
  align-items: flex-end;
  justify-content: center;
}

.dial__central-axis::after {
  content: "";
  width: 25px;
  height: 25px;
  background-color: var(--axis-color);
  border-radius: 50%;
  z-index: 1;
}

.central-axis__hour-arrow {
  position: absolute;
  height: 250px;
  width: 20px;
  border: 4px solid var(--border-color);
  background-color: var(--arrow-color);
  border-radius: 8px;
  transform-origin: 50% 238px 0;
  transition: transform 0.2s cubic-bezier(.4,2.08,.55,.44);
}

.central-axis__minute-arrow {
  position: absolute;
  height: 275px;
  width: 10px;
  border: 2px solid var(--border-color);
  background-color: var(--arrow-color);
  border-radius: 4px;
  transform-origin: 50% 263px 0;
  transition: transform 0.2s cubic-bezier(.4,2.08,.55,.44);
}

.central-axis__second-arrow {
  position: absolute;
  height: 295px;
  width: 5px;
  background-color: var(--secondary-color);
  border-radius: 2px;
  transform-origin: 50% 283px 0;
  transition: transform 0.2s cubic-bezier(.4,2.08,.55,.44);
}

.immovable {
  transition: none;
}